#game1-wrapper,
#game1-content,
#room-itself {
	width: 100%;
	height: 100%;
}

.popup-wrapper {
	position: absolute;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
	display: flex;
	justify-content: center;
	align-items: center;
}

.popup-content {
	width: 700px;
	height: 550px;
	background-color: #F9AFA1;
	border-radius: 3rem;
	padding: 50px;
	// position: relative;
	display: flex;
	flex-direction: column;
	justify-content: flex-start;
}

.popup-content > span {
	font-family: 'Abril Fatface';
	font-size: 3rem;
	color: #59316D;
	display: block;
	text-align: center;
}

.popup-content > p {
	margin-top: 3rem;
	font-style: italic;
	font-size: 2rem;
	text-align: center;
	flex-grow: 1;
}

#popup-btns {
	// position: absolute;
	// bottom: 0;
	// left: 0;
	// right: 0;
	// margin-bottom: 50px;
	flex-grow: 1;
	justify-self: flex-end;
	display: flex;
	flex-direction: row;
	justify-content: center;
	align-items: center;
}

#popup-btns button {
	font-family: 'Barlow Extrabold';
	font-size: 1.3rem;
	color: #59316D;
	margin: 20px;
	padding: 15px;
	border: none;
	border-radius: 2rem;
	position: relative;
	cursor: pointer;
	transition: 150ms linear;
}

button#no-go:hover,
button#go:hover {
	background-color: #59316D;
	color: #FFFFFF;
}

button#no-go::before {
	content: "";
	display: inline-block;
	margin-left: 15px;
	margin-right: 15px;
}

button#no-go {
	padding-right: 40px;
	border-bottom-right-radius: 0;
	background-color: #FFCCA9;
}

button#go {
	padding-left: 40px;
	border-bottom-left-radius: 0;
	background-color: #D6E3F4;
}

button#go::after {
	content: "";
	display: inline-block;
	margin-left: 15px;
	margin-right: 15px;
}

#question-counter {
	overflow: hidden;
	position: absolute;
	top: 0px;
	left: 0px;
	background-color: #CBE7DE;
	color: #59316D;
	max-width: 370px;
	width: 40vw;
	max-height: 330px;
	height: 32vh;
	display: flex;
	justify-content: center;
	align-items: center;
	font-family: 'Assistant';
	font-size: 2.5rem;
	font-weight: 300;
	border-radius: 0px 85px 200px 125px;
	transform: translateX(-100%);
	animation: slideAmountFromLeft 550ms 50ms cubic-bezier(0.3, 0.84, 0.37, 1) forwards;
}

#question-counter-info {
	margin-top: -50px;
	max-height: 100%;
	max-width: 100%;
}

#question-counter.max {
	background-color: #F9AFA1;
}

#question-counter > div > .numbers {
	font-family: 'Abril FatFace';
	font-size: 1.25em;
	line-height: 1.25em;
}

#question-counter > div > .numbers > span {
	font-size: 2em;
}

#question-counter > div > :last-child {
	line-height: 100%;
}

@keyframes slideAmountFromLeft {
	0% {transform: translateX(-100%);}
	100% {transform: translateX(0%);}
}
