#dialog-accusation-wrapper {
	position: absolute;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
	background-color: #FFFFFF80;
	z-index: 20;
	// Tive que colocar !important pq o bootstrap est sacaneando com o cdigo
	display: flex !important;
	justify-content: center;
	align-items: center;
	transition: 100ms cubic-bezier(.5, 0, .5, 1);
}

#dialog-accusation-wrapper[hidden] {
	opacity: 0;
	pointer-events: none;
	transition: 100ms 50ms cubic-bezier(.5, 0, .5, 1);
}

#dialog-accusation-wrapper[hidden] #dialog-accusation {
	transform: scale(0.95);
}

#dialog-accusation {
	transition: 150ms cubic-bezier(.5, 0, .5, 1);
	width: 70vw;
	min-height: 50vh;
	max-width: 1200px;
	max-height: 800px;
	padding: 30px;
	background-color: #F9AFA1;
	flex-shrink: 1;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: stretch;
	position: relative;
	font-size: 1.5rem;
	border-radius: 2em;
}

#dialog-accusation::before {
	content: "";
	display: block;
	width: 100%;
	height: 100%;
	position: absolute;
	// background-image: url('./../../img/bg-forma-laranja.svg');
	background-repeat: no-repeat;
	background-position: center;
	background-size: contain;
	transform: scale(2);
	z-index: -1;
}

#accusation-infos {
	flex-grow: 1;
	display: flex;
	justify-content: space-between;
	align-items: center;
}

#accusation-infos > * {
	flex-grow: 1;
	max-width: 50%;
}

#accusation-infos > :first-child {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	text-align: center;
	font-size: 1.5em;
}

#accusation-infos span[lang="pt-br"] {
	font-family: 'Barlow';
	font-weight: bold;
}

#accusation-infos span[lang="pt-br"]::after{
	content: "";
	display: block;
	margin: 15px 0px;
	border-bottom: 2px solid #fdc7b3;
}

#accusation-infos span[lang="en"] {
	font-family: 'Barlow light';
	font-style: italic;
	font-size: 0.9em;
}

#tips-received {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	font-family: 'Assistant';
	text-align: center;
	background-color: #FBBBA3;
	border-radius: 1.5em;
	// padding: 60px 20px 20px 20px;
	padding: 20px;
}

#tips-received > div:nth-child(n+3) {
	margin-top: 0.35em;
}

#accusation-btns {
	display: flex;
	justify-content: center;
	align-items: center;
}

#accusation-btns button {
	margin: 5px 15px;
	padding: 12px 30px;
	text-transform: unset;
	transition: 100ms linear;
	border-radius: 2em;
	font-family: 'Barlow ExtraBold';
	font-size: 1em;
}

#accusation-btns button:first-child {
	background-color: #FFCCA9;
	color: #59316D;
	border-bottom-right-radius: 0;
}

#accusation-btns button:first-child::before {
	content: "";
	margin-right: 25px;
}

#accusation-btns button:last-child {
	background-color: #D6e3F4;
	color: #59316D;
	border-bottom-left-radius: 0;
}

#accusation-btns button:last-child::after {
	content: "";
	margin-left: 25px;
}

#accusation-btns button:hover {
	background-color: #59316D;
	color: #FFFFFF;
}

#tips-received {
	position: relative;
}

#accusation-icon {
	position: absolute;
	width: 100%;
	height: 5vh;
	left: 0;
	top: 0;
	display: flex;
	justify-content: center;
	align-items: center;
	overflow: visible;
}

#accusation-icon > img {
	position: relative;
	top: -66%;
	height: 100%;
	object-fit: contain;
	flex: 1 1 0px;
}
